                                <div class="uc_c_item">
                                    <div v-if="collect.length==0" class="f16 mt30 tc color6 w mb30">您还没有任何收藏哦</div>
                                    <div v-else class="proBox" v-for="(item,index) in collect" :key="index">
                                            <el-image class="imgFix" :src="item.list_pic_url" fit="cover" @click="toCollectDetail(item.spec_id)"></el-image>
<!--                                            <img @click="toCollectDetail(item.spec_id)" class="imgFix" :src="item.list_pic_url">-->
                                        <div class="proTit">
                                            <div class="proName wto" @click="toCollectDetail(item.spec_id)">{{item.good_name}}</div>
                                            <div class="proSize f14 color9">规格：{{item.spec_name}}{{item.spec_unit}}</div>
                                            <div class="proBuy flex j-bet">
                                                <div class="colorPrice price">{{item.price_type}}{{item.price_cn}}/{{item.supply_unit}}</div>
