<script>
    import sideBar from '../../../components/usercenter/sideBar';
    import headTop from '../../../components/header/headTop';
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {getMyCollection,getMyOrderList,message} from '../../../service/api'

    export default {
        name: 'ucIndex',
        data(){
            return{
                collect:'',
                orderValue:[],
                message:[]
            }
        },
        components:{
            sideBar,
            headTop
        },
        async beforeMount(){
            this.getUserInfo();
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){
                //收藏
                var collectRes = await getMyCollection(4,1);
                this.collect = collectRes.status==12007?'':collectRes.data;

                //消息
                var messRes = await message('','');
                this.message = messRes.data;
                console.log(this.message);

                //订单状态-数量
                var order1 = await getMyOrderList('weifukuan','','');
                this.orderValue[0] = order1.count;

                var order2 = await getMyOrderList('yifukuan','','');
                var order3 = await getMyOrderList('peisong','','');
                this.orderValue[1] = order2.count+order3.count;

                var order4 = await getMyOrderList('daipinglun','','');
                this.orderValue[2] = order4.count;


                console.log(this.orderValue);

                this.$forceUpdate();
            },
            toLogin(){
                this.$router.push({path:"/login/login"});
            },
            toCollect(){
                this.$router.push({path:"/userCenter/collect/collect"});
            },
            toCollectDetail(specifications_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:specifications_id}});
            },
            toOrder(tar){
                if (this.login){
                    this.$router.push({path:"/userCenter/order/order",query:{activeName:tar}});
                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            toMessage(){
                this.$router.push({path:"/userCenter/message/message"});
            },

        },
        watch:{
            userInfo: function (value){
                if (!value || !value.user_id){
                    //value && value.user_id
                    this.initData();
                }
            }
        }

    }

</script>

<style scoped>
