    .uc_i_myInfo{width:100%;}
    .uc_i_myInfo h2{font-size: 20px;color:#333;margin-bottom:5px;}
    .ucI_a_block{display: block;}
    .ucI_a{color:#666;display: inline-block;border-right:1px solid #ddd;line-height: 20px;font-size: 14px;}
    .ucI_a:last-child{border:none;}
    .uc_i_order{width:50%;display: flex;flex-wrap: nowrap;justify-content: flex-end;}
    .ucI_item{width:100px;text-align: center;font-size: 14px;}
    .ucI_item:hover{cursor: pointer;color:#0bb8b2;}
    .ucI_item img{width:30px;height: 30px;display: block;margin:0 auto;}
    .ucI_text label{color:#f85a5b;padding-left: 5px;font-weight: bold;width:100%;font-size: 14px;}
    .uc_index_md{display: flex;flex-wrap: nowrap;}
    .ucP_notice{min-height: 200px;}
    .uB_hBox{
        display: flex;flex-wrap: nowrap;
    }
    .uB_half{
        width:50%;
    }
    .ucP_s_item{display: flex;flex-wrap: nowrap;background-color: #f5f5f5;padding:10px;width:100%;}
    .ucP_s_image{width:80px;text-align: center;}
    .ucP_s_image img{margin:0 auto;padding:0 5px;width:50px;height:50px;display:block;}
    .ucP_s_image span{width:100%;text-align: center;display:block;color:#999;}
    .ucP_s_info{color:#333;}
    .toLink{color:#666;font-size: 14px;}
    .toLink:hover{color: #0bb8b2;cursor: pointer;}

    /*消息通知*/
    .moreText{color:#666;font-size: 14px;font-weight: normal;}
    .moreText:hover{color:#0bb8b2;cursor: pointer;}
    .ucP_n_top{display: flex;flex-wrap: nowrap;justify-content: space-between;line-height: 30px;}
    .u_n_a{flex-shrink:0;max-width: 360px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
    }
    .u_n_a:hover{cursor: default;}
    .ucP_n_top label{width:100px;flex-shrink:1;color:#999;text-align: right;font-size: 13px;}
    /*我的收藏*/
    .uc_index_collect{min-height: 300px;}
    .uc_c_item{display: flex;flex-wrap: nowrap;}
    .proBox{
        background-color:#fff;
        border:1px solid #f0f0f0;
        padding:15px;
        width:194px;
        position: relative;
        max-width:194px;
        min-height:200px;
        margin-bottom:25px;
        margin-right:15px;
    }
    .proBox:nth-child(5n){
        margin-right:0;
    }
    .proBox:hover{
        border:1px solid #e4e4e4;
        box-shadow:0 2px 10px 0px #ddd;
    }
    .proBox:hover .proInfo{
        border:1px solid #e4e4e4;
        border-top:none;
        box-shadow:0 2px 10px 0px #ddd;
    }
    .proBox:hover .gallery{
        display:flex;
    }
    .proImg{
        width:100%;
        height:190px;
        line-height: 188px;
        margin-bottom:5px;
        text-align: center;
    }
    .proImg:hover{
        cursor: pointer;
    }
    /*.proImg a .imgFix{*/
    /*    width:180px;*/
    /*    height:180px;*/

    /*}*/
    /*.imgFix img{*/
    /*    line-height: 188px;*/
    /*}*/
    .imgFix{
        width:180px;
        height: 180px;
    }

    .proInfo{
        position:absolute;
        top:210px;
        left:-1px;
        z-index: 10;
        background-color:#fff;
        width:194px;
        padding:0 15px 15px 15px;
    }
    .proName{
        color:#333;
        font-size: 14px;
    }
    .price{
        font-size: 18px;
        font-size: 14px;
    }
    .proTit{
        line-height: 24px;
        font-size: 14px;
    }

</style>



